.newsletterList h2{font-size:22px;font-weight:700;text-transform:uppercase}.newsletterList h2:after{content:"";display:block;width:70px;height:4px;background:var(--color-red);margin-top:15px}.newsletterListGrid{display:grid;grid-template-columns:repeat(3, 1fr);grid-gap:32px;margin-top:20px;margin-bottom:60px}.newsletterListGrid:last-child{margin-bottom:0}@media(max-width: 1024px){.newsletterListGrid{grid-template-columns:repeat(2, 1fr)}}@media(max-width: 768px){.newsletterListGrid{grid-template-columns:repeat(1, 1fr)}}